import React from 'react'
// import { useState, useEffect } from 'react';
import DateCountdown from 'react-date-countdown-timer';
import "./timer.scss"
import { useEffect, useState } from "react";
import innerText from 'react-innertext';


export default function Topbar(){
  
  var audio = new Audio("../.././music.mp3")
  var x = document.getElementById("myAudio"); 

function playAudio() { 
  x.play(); 
} 

function pauseAudio() { 
  x.pause(); 
} 
    const [days, setDays] = useState(10)
  const [hours, setHours] = useState(10)
  const [minutes, setMinutes] = useState(10)
  const [seconds, setSeconds] = useState(10)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
   
    
    const countdown = () => {
      const endDate = new Date("January 1, 2022 00:00:00").getTime()
      const today = new Date().getTime()

      const timeDiff = endDate - today

      const seconds = 1000
      const minutes = seconds * 60
      const hours = minutes * 60
      const days = hours * 24

      let timeDays = Math.floor(timeDiff / days)
      let timeHours = Math.floor((timeDiff % days) / hours)
      let timeMinutes = Math.floor((timeDiff % hours) / minutes)
      let timeSeconds = Math.floor((timeDiff % minutes) / seconds)

      timeHours = timeHours < 10 ? "0" + timeHours : timeHours
      timeMinutes = timeMinutes < 10 ? "0" + timeMinutes : timeMinutes
      timeSeconds = timeSeconds < 10 ? "0" + timeSeconds : timeSeconds

      setDays(timeDays)
      setHours(timeHours)
      setMinutes(timeMinutes)
      setSeconds(timeSeconds)
      setIsLoading(false)

      if(days === 0 && hours === 0 && minutes ===0 && seconds===0 ){
        innerText(
          <div>
              Happy <strong> New Year</strong>!
              I am 19 years old!
          </div>
        )
      };
    }

    setInterval(countdown, 1000)
  }, [])

    return(
    
        <>
        {isLoading ? (
          <div className="loading">
            <div className="spinner"></div>
          </div>
        ) : (
          <section className="container">
            <h1>New Year Countdown 2022</h1>
            
            <div className="countdown">
              <article>
                <p>{days}</p>
                <h3>Days</h3>
              </article>
              <article>
                <p>{hours}</p>
                <h3>Hours</h3>
              </article>
              <article>
                <p>{minutes}</p>
                <h3>Minutes</h3>
              </article>
              <article>
                <p>{seconds}</p>
                <h3>Seconds</h3>
              </article>
            </div>
            <div className='lethanhdat'>
              <a href='https://wortzzzzz.github.io/justWortz/'>click here to know the author</a>
              <p>Click the buttons to play or pause the audio.</p>
            
        
            </div>
          </section>
        )}  
      </>
    )
        }